a, .as-link {
  text-decoration: none;
  color: #269;
  line-height: 1.2em;
  display: inline-block;
  vertical-align: baseline;
  zoom: 1;
  *display: inline;
  *vertical-align: auto;
  -webkit-transition: color 0.3s;
  -moz-transition: color 0.3s;
  -ms-transition: color 0.3s;
  -o-transition: color 0.3s;
  transition: color 0.3s; }
  a.main, a:hover, .as-link.main, .as-link:hover {
    color: #1d5884;
    text-decoration: underline;
    background-repeat: repeat-x;
    background-position: left bottom; }
  a.big-button, .as-link.big-button {
    min-width: 30%;
    border-radius: 3px;
    -webkit-transition: all, 0.2s;
    -moz-transition: all, 0.2s;
    -ms-transition: all, 0.2s;
    -o-transition: all, 0.2s;
    transition: all, 0.2s;
    color: #fff;
    cursor: pointer;
    font-size: 1.2em;
    line-height: 24px;
    padding: 8px 17px;
    text-transform: uppercase;
    letter-spacing: 2px;
    font-weight: bold;
    display: block;
    text-align: center;
    border-width: 0;
    border-style: solid;
    border-color: #8ab;
    background: #6f8f9f;
    box-shadow: inset 0 -1px 0px rgba(0, 0, 0, 0.1); }
    a.big-button:hover, .as-link.big-button:hover {
      background: #608090; }
    a.big-button:active, .as-link.big-button:active {
      box-shadow: inset 0 1px 0px rgba(0, 0, 0, 0.1); }
    a.big-button.red, .as-link.big-button.red {
      border-color: #ddacac;
      background: #a65; }
      a.big-button.red:hover, .as-link.big-button.red:hover {
        background: #b76; }
    a.big-button.gray, .as-link.big-button.gray {
      border-color: #f4f4f4;
      color: #ffffff;
      background: #565656; }
      a.big-button.gray:hover, .as-link.big-button.gray:hover {
        background: #676767; }
    a.big-button.blue, .as-link.big-button.blue {
      border-color: #ace;
      background: #678; }
      a.big-button.blue:hover, .as-link.big-button.blue:hover {
        background: #789; }
    a.big-button.green, .as-link.big-button.green {
      border-color: #ddeed0;
      background: #696; }
      a.big-button.green:hover, .as-link.big-button.green:hover {
        background: #797; }

